@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Lato', sans-serif;

    font-size: 0.5vw;
    line-height: 1.2;
    background-color: rgba(255, 80, 80, 1);
    background-image: -webkit-linear-gradient(45deg, white 25%, transparent 25.5%, transparent 50%, white 50.5%, white 75%, transparent 75.5%, transparent);
    background-image: linear-gradient(45deg, white 25%, transparent 25.5%, transparent 50%, white 50.5%, white 75%, transparent 75.5%, transparent);
    background-size: 80px 80px;

    -webkit-animation-name: body-movement;

            animation-name: body-movement;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes body-movement {
    from {
        background-position-x: 0;
    }
    to {
        background-position-x: 80px;
    }
}

@keyframes body-movement {
    from {
        background-position-x: 0;
    }
    to {
        background-position-x: 80px;
    }
}

@media screen and (min-width: 900px) {
  body {
    font-size: 4px;
  }
}

.p-5 {
  padding: 5%;
}

.d-block {
    background-color: white;
    border-radius: 100px;
    position: absolute;
    display: block;
    width: 80%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    text-align: center;
    max-width: 800px;
    max-height: 600px;
}

.font-size-huge {
    font-size: 35em;
    font-family: 'Lobster', cursive;
}

.font-size-large {
  font-size: 22em;
}


.flex {
  display: flex;
  flex-direction: column;
}
